<template>
    <div>
        <div class="container grid" style="margin-top: 2rem">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget"></h2>
                    <hr class="seg-widget"/>

                    <!---->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10" style="padding-bottom: 3rem">
                <router-view></router-view>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";

    export default {
        name: "online_talk",
        data() {
            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "",
                        act: "act",
                        type: "route",
                        link: "/online_talk/my_class?from=online_talk",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/check_work",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/students/my_class?from=students",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/resource",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/tests",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/majors",
                    }
                ]

            }
        },
        components: {
            "aside-nav-widget": AsideNav,
        }
    }
</script>

<style scoped>
    @import "../css/seg-widget.css";
    @import "../css/base-title-widget.css";
    @import "../css/pannel-widget.css";
</style>


