<template>
    <div class="confirm-widget" :state="pannel.state">
        <div class="c-w-cont">
            <i class="c-w-ico" :state="pannel.icon_state" v-show="pannel.icon_state"></i>
            <span class="c-w-text">{{pannel.text}}</span>
        </div>
        <div class="c-w-btns" v-if="pannel.mode === 'confirm'">
            <a class="c-w-btn" type="cancel"></a><a class="c-w-btn" type="ok"></a>
        </div>
    </div>
</template>

<script>
    export default {
        name: "confirm-widget",
        data(){
            return {

                /**/
                pannel: {
                    state: "hidden",
                    mode: "",
                    text: "",
                    icon_state: ""
                },

                /**/
                matte: null

            }
        },
        methods: {

            /**/
            toast(text, duration, icon_state){
                if(this.pannel.state !== "show"){
                    this.matte && this.matte.show();
                    this.pannel = {
                        state: "show",
                        mode: "toast",
                        text: text
                    };
                    if(duration){
                        setTimeout(()=>{
                            this.close();
                        }, duration)
                    }
                    this.pannel.icon_state = icon_state || "";
                }
            },

            /**/
            load(text, method){
                if(method === "end"){//
                    this.pannel.text = text;
                    this.pannel.icon_state = "success";
                    setTimeout(()=>{
                        this.close();
                    }, 1000)
                }else{//
                    this.matte && this.matte.show();
                    this.pannel = {
                        state: "show",
                        mode: "toast",
                        text: text,
                        icon_state: "loading"
                    };
                }
            },

            /**/
            close(){
                this.matte && this.matte.hide();
                this.pannel = {
                    state: "hidden",
                    mode: "",
                    text: "",
                    icon_state: ""
                };
            }

        },
        mounted(){
            this.matte = this.$B.widgets.Matte;
            this.$B.widgets.regist("Confirm", this);
        }
    }
</script>

<style scoped>
    @import "../../css/confirm-widget.css";
</style>


