                aside_nav_data: [
                    {
                        name: "",
                        type: "route",
                        link: "/online_talk/my_class?from=online_talk",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/check_work",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/students/my_class?from=students",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/resource",
                    },
                    {
                        name: "",
                        type: "route",
                        act: "act",
                        link: "/tests",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/majors",
                    }
                ],

                /**/
                students: [
                    {
                        name: "",
                        progress: "1",
                        tesk: "1-1-2",
                        team: "01",
                        week_progress: "",
                        month_progress: "",
                    }
                ],

            }
        },
        components: {
            "aside-nav-widget": AsideNav,
        }
    }
</script>

<style scoped>
    @import "../css/seg-widget.css";
    @import "../css/base-title-widget.css";
    @import "../css/pannel-widget.css";
    @import "../css/no-result-widget.css";
</style>


